















import { defineComponent } from "@vue/composition-api";

export default defineComponent({
  name: "PageFooter",
  components: {
    AppButton: () => import("@/views/widget/app-button.vue")
  },

  setup(_, { root, emit }) {
    const referPeople = () => {
      emit("on-show", true);
    };

    const referProduct = () => {
      root.$router.push("/products");
    };

    return {
      referPeople,
      referProduct
    };
  }
});
